.rlr-accordion__item {
	border: 2px solid var(--gray-02);
	border-radius: var(--spacing-5) var(--spacing-5) 0 0;
}
.rlr-accordion__item.accordion-item:first-of-type {
	border-radius: var(--spacing-5) var(--spacing-5) 0 0;
}
.rlr-accordion__body {
	padding: var(--spacing-9) var(--spacing-7);
}
.rlr-accordion__badge {
	display: flex;
	min-width: 2rem;
	min-height: 2rem;
	align-items: center;
	justify-content: center;
	margin-right: var(--spacing-5);
	background: var(--brand);
	border-radius: 50%;
	color: var(--brand-02);
}
.rlr-accordion__button.accordion-button {
	padding: var(--spacing-7) var(--spacing-5);
	border-bottom-color: var(--gray-03);
	background: var(--gray-00);
	border-radius: var(--spacing-5) var(--spacing-5) 0 0;
	color: var(--heading-color);
}
.rlr-accordion__button.accordion-button::after {
	background: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTEiIGhlaWdodD0iMjAiIGZpbGw9Im5vbmUiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+PHBhdGggZD0iTTEuNSAxOC41bDgtOC41LTgtOC41IiBzdHJva2U9IiNDRUQ0RDkiIHN0cm9rZS13aWR0aD0iMiIgc3Ryb2tlLW1pdGVybGltaXQ9IjEwIiBzdHJva2UtbGluZWNhcD0icm91bmQiIHN0cm9rZS1saW5lam9pbj0icm91bmQiLz48L3N2Zz4=") no-repeat;
}
.rlr-accordion__button.accordion-button:not(.collapsed)::after {
	transform: rotate(90deg);
}
