/*!  ==============================================================
<!-- 5.3 Button ==== -->
============================================================== **/
.rlr-button {
	--border-radius: 0;
	--button-background-color: transparent;
	--padding: 0.5em;
	--button-text-color: var(--white);
	--button-border-color: var(--neutral-color);
	--button-font-weight: 300;
	--button-font-weight-bold: 500;
	background-color: var(--button-background-color);
	border-radius: var(--border-radius);
	color: var(--white);
	white-space: nowrap;
}
.rlr-button:focus {
	box-shadow: none;
}
.rlr-button--google {
	display: flex;
	height: 44px;
	box-sizing: border-box;
	align-items: center;
	justify-content: center;
	border: 1px solid #d0d5dd;
	background: var(--white);
	border-radius: 8px;
	box-shadow: 0 1px 2px rgba(16, 24, 40, 0.05);
	color: var(--heading-color);
	text-align: center;
}
.rlr-button--google::before {
	display: inline-block;
	width: 25px;
	height: 25px;
	margin-right: 10px;
	background: url("../assets/images/google-signin.png") no-repeat 0 0;
	content: "";
}
.rlr-button--form-tooltip .rlr-icon-font {
	color: var(--brand);
	font-size: 1.5rem;
}
.rlr-button--form-tooltip .rlr-icon-font:hover {
	color: var(--brand-04);
	transition: color 0.3s;
}
.rlr-button--fullwidth {
	width: 100%;
}
.rlr-button--primary {
	width: 100%;
	height: 44px;
	background: var(--brand);
	border-radius: var(--spacing-3);
	color: var(--white);
}
.rlr-button--primary:hover {
	color: var(--white);
}
.rlr-button--search {
	padding: 40px;
}
.rlr-button--large {
	padding: var(--spacing-5) var(--spacing-8);
}
.rlr-button--large .rlr-icon-font {
	font-size: 2rem;
	line-height: 0;
}
.rlr-button--medium {
	padding: var(--spacing-5) var(--spacing-6);
	font-weight: var(--button-font-weight-bold);
}
.rlr-button--small {
	padding: var(--spacing-3) var(--spacing-5);
}
.rlr-button--rounded {
	border-radius: 2rem;
}
.rlr-button--circle {
	border-radius: 50%;
	width: 3rem;
	height: 3rem;
	padding: 1rem;
}
.rlr-button--carousel {
	border-radius: 50%;
	border: 2px solid var(--gray-04);
	background: transparent;
	color: var(--neutral-color);
}
@media (min-width: 576px) {
	.rlr-button--carousel {
		width: 4.5rem;
		height: 4.5rem;
		padding: 1rem;
	}
	.rlr-button--carousel .rlr-icon-font {
		font-size: 2rem;
		line-height: 1.2;
	}
