/*stylelint-disable*/
// Mixins
@import "./scss/mixins";
@import "./scss/variables";
@import "./scss/navigation.core";
@import "./scss/navigation.body";
@import "./scss/navigation.menu";
@import "./scss/navigation.submenu.indicator";
@import "./scss/navigation.text";
@import "./scss/navigation.dropdown";
@import "./scss/navigation.megamenu";
@import "./scss/navigation.megamenu.grid";
@import "./scss/navigation.megamenu.list";
@import "./scss/utilities";

.rlr-navigation {
  transition: all 300ms ease-in-out;

  &__body-header {
    padding: 0;
  }

  &__body {
    display: flex;
    flex-direction: column;
    padding: 1.875rem;
    transition: all 300ms ease-in-out;

    @media (min-width: 992px) {
      flex-direction: row;
      padding: 1.875rem 0;
    }
  }

  &__button-login {
    margin-right: calc(2.562rem - var(--spacing-4));
    color: var(--heading-color);
  }

  &__button-signup {
    color: var(--white);
    font-weight: 300;

    @include media-breakpoint-down(lg) {
      min-width: 100%;
    }
  }

  &__link {
    @extend %type-sub-title-medium;

    height: auto;
    padding: 0;
    color: var(--heading-color);
    line-height: normal;
  }

  &__menu.navigation-menu {
    align-items: center;
  }

  &__menu--main-links {
    display: flex;
    flex-direction: column;

    @media (min-width: 992px) {
      flex-direction: row;
      gap: var(--spacing-5);
    }
  }

  .rlr-logo__navbar-brand {
    margin-right: var(--spacing-10);
  }

  .rlr-svg-icon__header-search {
    fill: var(--body-color);
    stroke: var(--body-color);
  }

  &--wp {
    & .navigation-col > ul {
      padding-top: var(--spacing-7); // only for WordPress Menu
    }
  }

  .sign-up-in {
    width: 100%;
    flex-direction: column;
    padding: 10px;

    a {
      width: 100%;
    }

    .sign-up {
      margin-bottom: 15px;
      background-color: #28b0a6;
      color: #fff;

      &:hover {
        border: 2px solid #28b0a6;
      }
    }

    .sign-in {
      background-color: #eefbfa;
      color: #22968e;

      &:hover {
        border: 2px solid #eefbfa;
      }
    }
  }

  .navigation-list-heading a {
    color: var(--body-color);
    font-size: 14px;
  }

  .navigation-col div > span > a {
    font-size: 1rem;
    font-weight: 500;
    line-height: 1.25;
  }

  .navigation-list .navigation-list-heading > a {
    @extend %type-lead-medium;

    color: var(--gray-07);
  }

  .navigation-list > li a:hover {
    background-color: transparent;
  }

  .navigation-list :not(.navigation-list-heading) > a::before {
    content: "";
  }

  .navigation-body {
    padding: var(--spacing-5) 0;

    .navigation-col > ul > li > a {
      padding: var(--spacing-5) var(--spacing-4);
    }

    .navigation-link {
      padding: 0.5rem;
      font-size: 1.125rem;
      font-weight: 400;

      & .avatar {
        width: 2rem;
      }
    }
  }

  .rlr-navigation--wp {
    & .navigation-megamenu-container {
      padding: $megamenu-container-padding !important;
    }
  }

  & .navigation-row {
    & > [class^="navigation-col"] {
      position: relative;
      width: 100%;
      min-height: 20px;
      margin-bottom: 1rem;
    }

    &:last-of-type [class^="navigation-col"] {
      list-style: none;
    }

    @include media-breakpoint-down(md) {
      padding-left: 0;
    }
  }

  .rlr-icon-text {
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 1.5rem;
    column-gap: var(--spacing-5);

    &__subtext {
      color: #667085;
      font-size: 0.875rem;
      font-weight: 400;
    }
  }

  .rlr-navigation-list-heading {
    &--tall {
      margin-bottom: 1rem;
    }
  }

  .rlr-step__icon {
    width: 3rem;
    height: 3rem;
    background: var(--brand-01);

    svg {
      fill: var(--brand);
      stroke: var(--white);
    }
  }

  & .rlr-svg-icon__header-language {
    svg {
      fill: none;
      stroke: var(--body-color);
    }
  }

  .align-to-right {
    .navigation-link {
      column-gap: 0.5rem;

      @extend %type-lead-light;

      line-height: $navigation-height;

      @media (min-width: 992px) {
        padding: 0.5rem;
        white-space: nowrap;
      }
    }
  }

  .rlr-navigation__link--so {
    color: var(--brand);
    font-weight: 500 !important;
  }

  .navigation-button-toggler {
    width: auto;
    height: auto;

    span i {
      padding: 0.5rem 0;
      font-size: 2rem;
    }
  }

  .navigation-megamenu {
    position: static;
    width: 100%;
    max-height: 0;
    padding-left: 0;
    border-radius: 0 0 var(--spacing-7) var(--spacing-7);

    @include transition(max-height 0.2s cubic-bezier(0, 1, 0, 1));

    &.is-visible {
      max-height: $megamenu-max-height;
      border-bottom: solid 1px $megamenu-border-color;

      @include transition(max-height 1s ease-out);
    }

    @media (min-width: $landscape-width) {
      position: absolute;
      border-top: solid 0 transparent;
      box-shadow: $megamenu-box-shadow-landscape;
      opacity: 0;

      @include transition(opacity 0.3s linear, max-height 0s linear 0.4s);

      // Megamenu container
      &-container {
        display: table;
        padding: 0;
      }

      &-container,
      .navigation-dropdown {
        padding-top: 0.5rem;

        @include media-breakpoint-up(lg) {
          transition: opacity -0.1s linear, max-height 0s linear -0.1s;
        }
      }

      &.is-visible {
        border-bottom: none;
        opacity: 1;

        @include transition(opacity 0.3s linear, max-height 0s linear);
      }

      // Widths
      &.navigation-megamenu-half {
        width: 50%;
      }

      &.navigation-megamenu-quarter {
        width: 25%;
      }
    }

    .scroll-momentum & {
      @include transform(translateZ(0));

      @media (min-width: $landscape-width) {
        @include transform(none);
      }
    }

    // Fix for bootstrap grid
    [class*="container"] {
      width: 100%;
    }

    [class*="container"] [class*="col-"] {
      padding: 0;
    }
  }

  .navigation-submenu {
    border-radius: 0 1rem 1rem 1rem;
  }

  /* Transparant Navigation
================================ */

  &--transparent {
    position: absolute;
    z-index: 1;
    top: 0;
    width: 100%;
    background: transparent;

    @include media-breakpoint-up(lg) {
      & .navigation-link {
        color: var(--white);

        & > .submenu-indicator:after {
          border-bottom: 1px solid var(--white);
          border-right: 1px solid var(--white);
        }
      }

      & .navigation-col > ul {
        margin-top: 1.5rem;
      }

      & .rlr-svg-icon__header-language svg {
        fill: none;
        stroke: var(--white);
      }

      & .navigation-dropdown-item:first-child {
        padding-top: 0.875rem;
      }

      & .rlr-header-search__icon .rlr-icon-font {
        color: var(--white);
      }

      & .rlr-header-search__wrapper .rlr-header-search__results {
        border-radius: 1rem;
        padding-top: 1.5rem;
      }
    }

    @include media-breakpoint-up(xxl) {
      & #rlrSearchBox::placeholder {
        color: transparent;
      }

      & .rlr-header-search.open #rlrSearchBox::placeholder {
        color: var(--white);
      }

      & .rlr-banner-search {
        width: 90%;
        margin: 0 auto;
      }

      & .form-control {
        color: var(--white);
      }

      .rlr-header-search.open .form-control {
        border-bottom: 2px solid var(--white);

        & #rlrSearchBox::placeholder {
          color: var(--gray-02);
        }
      }
    }

    & .navigation-item:not(.navigation-brand-text).is-active .navigation-link,
    .navigation-item:not(.navigation-brand-text):focus .navigation-link,
    .navigation-item:not(.navigation-brand-text):hover .navigation-link {
      color: #1b98a0;
    }

    & .navigation-submenu {
      border-radius: 1rem;
    }

    &-link {
      color: var(--white);
    }

    & .navigation-item:not(.navigation-brand-text).is-active .navigation-link,
    .navigation-item:not(.navigation-brand-text):focus .navigation-link,
    .navigation-item:not(.navigation-brand-text):hover .navigation-link {
      color: var(--brand);
    }
  }
}

.rlr-sVGIcon {
  cursor: pointer;
}

.rlr-header-search {
  &__button {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(-5px, -50%);
  }

  &__wrapper {
    padding: 10px 5px;

    .rlr-header-search__results {
      margin-left: unset;
    }
  }

  &__wrapper .rlr-header-search.open .rlr-banner-input-group__input {
    padding-right: 34px;
    font-size: 14px;
  }
}

@media (min-width: 992px) and (max-width: 1200px) {
  .rlr-header-search__wrapper .rlr-header-search.open .rlr-banner-input-group__input {
    min-width: 175px;
  }

  .container {
    min-width: 98%;
  }
}

@media (max-width: 991px) {
  .navigation-body {
    padding: 0;
  }

  .rlr-navigation .navigation-list > li.navigation-list-heading > a {
    font-size: 14px;
  }

  .rlr-navigation__body-header {
    padding: var(--spacing-5) var(--spacing-3);
  }

  .navigation-header {
    padding: var(--spacing-5) var(--spacing-3);
  }

  .rlr-header-search__wrapper {
    width: 100%;

    ul {
      width: 100%;

      i {
        > span {
          position: absolute;
          top: 50%;
          left: 30px;
          display: block;
          content: "Search";
          font-size: 1.125rem;
          font-weight: 400;
        }
      }
    }
  }

  .avatar {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
  }

  .rlr-header-search__button {
    padding-right: 10px;
    transform: translate(5px, -50%);
  }
}

.rlr-icon-font {
  &--megamenu {
    color: var(--brand);
    font-size: 1.5rem;
    line-height: 1;
  }
}
